:root {
  --ButtonGreen: #bbf330;
  --DarkGreen: #7eb101;
  --YellowMain: #ffe550;
  --Black: #2c2c2c;
  --White: #fefefe;
  --neutral-dark-gray: #505050;
}

html {
  scroll-behavior: smooth;
}

body {
  color: var(--Black);
  background-color: var(--White);
  font-family: Roboto, sans-serif;
}

button {
  color: inherit;
  cursor: pointer;
  border: none;
  padding: 0;
  font-family: inherit;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
}

img {
  object-fit: contain;
  display: block;
}

* {
  margin-top: 0;
  margin-bottom: 0;
}

.link {
  text-decoration: none;
}

.list {
  list-style: none;
}

input {
  color: inherit;
  outline: rgba(0, 0, 0, 0);
  font-family: inherit;
}

.container {
  max-width: 320px;
  margin: 0 auto;
  padding: 0 20px;
}

